import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll,
  Group,
  Gallery,
  Header,
} from "@vkontakte/vkui";

import { Form, Statistics, StatisticsGroups } from "../components";
import {
  OperationsHistoryItem,
} from "../items";
import {
  dataHistory,
} from "../helpers";

const Profile = ({ id, networkStatus, updateConnection, stub, snackbar, openAlert }) => {
  let dataStickersAll = [];

  const [activeTab, setActiveTab] = useState("home");
  const [phoneNumber, setPhoneNumber] = useState("+7");
  const [editStatus, setEditStatus] = useState(false);

  function tab(type) {
    switch (type) {
      case "home":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "statistics":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "statistics-groups":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
    }
  }

  function onChangePhoneNumber(e) {
    const value = e.target.value.trim();

    setPhoneNumber(value);
    console.log("phoneNumber", phoneNumber);
    
  }

  const editPhoneNumber = (type) => {
    switch (type) {
      case "edit":
        setEditStatus(true);
        break;
      case "save":
        setEditStatus(false);
        openAlert("Вы успешно изменили номер QIWI Кошелька")
        break;
    }
    
  }




  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple>VIP Стикеры</PanelHeaderSimple>

      <>
        {networkStatus ? (
          stub(
            "Отсутствует интернет - соединение!",
            updateConnection,
            "Повторить"
          )
        ) : (
          <>
            <FixedLayout vertical="top" filled={true}>
              <Tabs>
                <TabsItem
                  onClick={() => tab("home")}
                  selected={activeTab === "home"}
                >
                  Профиль
                </TabsItem>
                <TabsItem
                  onClick={() => tab("statistics")}
                  selected={activeTab === "statistics"}
                >
                  Статистика
                </TabsItem>
                <TabsItem
                  onClick={() => tab("statistics-groups")}
                  selected={activeTab === "statistics-groups"}
                >
                  Все группы
                </TabsItem>
              </Tabs>
            </FixedLayout>
            <>
              {activeTab === "home" ? (
                <Div
                  style={{
                    marginTop: 55,
                  }}
                >
                  <Form
                  phoneNumber={phoneNumber}
                  onChangePhoneNumber={onChangePhoneNumber}
                  editStatus={editStatus}
                  editPhoneNumber={editPhoneNumber}
                  />
                </Div>
              ) : (
                ""
              )}

              {activeTab === "statistics" ? (
                <Div
                  style={{
                    marginTop: 55,
                  }}
                >
                  <Statistics
                  
                  />
                </Div>
              ) : (
                ""
              )}

              {activeTab === "statistics-groups" ? (
                <Div
                  style={{
                    marginTop: 55,
                  }}
                >
                  <StatisticsGroups
                  
                  />
                </Div>
              ) : (
                ""
              )}
            </>

            {snackbar}
          </>
        )}
      </>
    </Panel>
  );
};

export { Profile };
