import React from "react";
import { SimpleCell, Button, Div, Link } from "@vkontakte/vkui";

const StickersAllItem = ({
  id,
  name,
  price,
  icon,
  author,
  description,
  amount,
}) => {
  return (
    <SimpleCell
      className="myItem"
      before={icon}
      // after={
      //   <Link id={id} mode="tertiary">
      //     купить
      //   </Link>
      // }
      description={
        <Div className="div">
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "start",
              marginTop: 5,
              marginBottom: 5,
              padding: 0,
            }}
          >
            <Link href={"https://vk.com/kirill_1131"} target="_blank">
              {author}
            </Link>
          </Div>
          <Button
            size="m"
            onClick={null}
            style={{
              backgroundColor: "green",
              color: "white",
              borderRadius: "6px",
              width: "120px",
            }}
          >
            Купить за {price} ₽
          </Button>
        </Div>
      }
    >
      {name}
    </SimpleCell>
  );
};

export { StickersAllItem };
