import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll,
  Group,
  Gallery,
  Header,
} from "@vkontakte/vkui";

import { StickersAllItem } from "../items";
import { dataAnime,  dataCartoons, dataFilms, dataGames, dataTVSeries} from "../helpers";


const Shop = ({ id, networkStatus, updateConnection, stub, snackbar }) => {
  let dataStickersAll = [];
  const [activeTab, setActiveTab] = useState("all-stickers");
  const [slideIndex, setSlideIndex] = useState(1);

  dataStickersAll = [
    ...dataCartoons,
    ...dataFilms,
    ...dataTVSeries,
  ];


  function tab(type) {
    switch (type) {
      case "all-stickers":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "games":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "anime":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "cartoons":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "films":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "tv-series":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
    }
  }

  const renderDataStickersAll = dataStickersAll.map((item, index) => {
    return (
      <StickersAllItem
        key={index}
        id={index + 1}
        name={item.name}
        author={item.author}
        description={item.description}
        price={item.price}
        icon={item.icon}
        amount={item.amount}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple>VIP Стикеры</PanelHeaderSimple>


      <Gallery
        // timeout="2000"
        slideIndex={slideIndex}
        onChange={(slideIndex) => setSlideIndex(slideIndex)}
        slideWidth="80%"
        align="center"
        style={{
          height: "220px",
          marginTop: "10px",
        }}
      >
        <Div className="iconSliderNaruto"></Div>
        <Div className="iconSliderGravityFalls"></Div>
        <Div className="iconSliderMinecraft"></Div>
      </Gallery>

      <>
        <HorizontalScroll>
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              flexDirection: "row",
              margin: 10,
              userSelect: "none",
              marginRight: 10,
              marginLeft: 10
            }}
          >
            <Div
              onClick={() => tab("all-stickers")}
              className={activeTab === "all-stickers" ? "tabGreen" : "tabWhite"}

            >
              Все
            </Div>
            <Div
              onClick={() => tab("anime")}
              className={activeTab === "anime" ? "tabGreen" : "tabWhite"}
            >
              Аниме
            </Div>
            <Div
              onClick={() => tab("games")}
              className={activeTab === "games" ? "tabGreen" : "tabWhite"}
            >
              Игры
            </Div>
            <Div
              onClick={() => tab("cartoons")}
              className={activeTab === "cartoons" ? "tabGreen" : "tabWhite"}
            >
              Мультики
            </Div>
            <Div
              onClick={() => tab("films")}
              className={activeTab === "films" ? "tabGreen" : "tabWhite"}
            >
              Фильмы
            </Div>
            <Div
              onClick={() => tab("tv-series")}
              className={activeTab === "tv-series" ? "tabGreen" : "tabWhite"}
            >
              Сериалы
            </Div>
          </Div>
        </HorizontalScroll>
        {networkStatus ? (
          stub(
            "Отсутствует интернет - соединение!",
            updateConnection,
            "Повторить"
          )
        ) : (
          <>
            <>
              {activeTab === "all-stickers" ? (
                <>
                  <Div>
                    <>
                      {!renderDataStickersAll.length ? (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      ) : (
                        <Div
                          style={{
                            paddingLeft: 10,
                            paddingRight: 10,
                          }}
                        >
                          {" "}
                          {renderDataStickersAll}
                        </Div>
                      )}
                    </>
                  </Div>
                </>
              ) : (
                ""
              )}

              {activeTab === "cartoons" ? (
                <>
                  <Div
                    style={{
                      marginTop: 55,
                    }}
                  >
                    {/* <ExchangeSell
                          durkaCoins={formatCoins(durkaCoins)}
                          course={course}
                          addExchangeOffer={addExchangeOffer}
                          onChangeAction={onChangeAction}
                          costOneDC={costOneDC}
                          inputDurkaCoins={inputDurkaCoins}
                          qiwiAccount={qiwiAccount}
                        /> */}
                    ТЕСТ
                  </Div>
                </>
              ) : (
                ""
              )}
            </>

            {snackbar}
          </>
        )}
      </>
    </Panel>
  );
};

export { Shop };
