import React from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Link,
  InfoRow,
  Input,
  FormLayoutGroup,
  FormLayout,
  Button,
} from "@vkontakte/vkui";
import Icon28WriteOutline from "@vkontakte/icons/dist/28/write_outline";

const Form = ({
  phoneNumber,
  onChangePhoneNumber,
  editStatus,
  editPhoneNumber

}) => {
  return (
    <Div
      style={{
        margin: 10,
      }}
    >
      <Group>
        <Cell>
          <Div
            style={{
              marginBottom: 5,
              marginTop: 5,
            }}
          >
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              }}
            >
              Введите номер QIWI Кошелька
            </Div>

            <Div
              style={{
                display: "flex",
                alignItems: "center",
                flexDirection: "row",
              }}
            >
             
              <Div 
              style={{
                flexGrow: 10,
              }}>
                <Input
                value={phoneNumber}
                onChange={(e) => onChangePhoneNumber(e)}
                type="text"
                disabled={editStatus ? false : true}
                
              ></Input>
              </Div>
              
              <Div
               style={{
                flexGrow: 0.5,
                marginLeft: 25
              }}
              > 
                <Icon28WriteOutline
                fill="green"
                onClick={() => editPhoneNumber("edit")}
              />
              </Div>
            </Div>
          </Div>

          <Div
            style={{
              marginBottom: 5,
              marginTop: 10,
            }}
          >
            <Group separator="show">
              <Button
                style={{ backgroundColor: "green", color: "white" }}
                onClick={() => editPhoneNumber("save")}
                mode="commerce"
                size="xl"
                stretched
              >
                Сохранить
              </Button>
            </Group>

            <Group>
              <Div
                style={{
                  marginTop: 10,
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  flexDirection: "column",
                }}
              >
                На указанный вами номер, будут приходить выплаты. 
              </Div>
            </Group>
          </Div>
        </Cell>
      </Group>
    </Div>
  );
};

export { Form };
