import React, { useState, useEffect } from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import Icon16Down from "@vkontakte/icons/dist/16/down";
import Icon16Up from "@vkontakte/icons/dist/16/up";
import { formatCoins } from "../helpers";

const OperationsHistoryItem = ({
  senderUrl,
  amount,
  senderName,
  senderPhoto,
  createdAt,
  formatDate,
}) => {
  return (
    <Cell
      before={
        senderPhoto ? (
          <Link href={senderUrl} target="_blank">
            <Avatar src={senderPhoto} />
          </Link>
        ) : (
          <Avatar src={"https://vk.com/images/camera_200.png"} />
        )
      }
      description={
        <Div style={{ marginLeft: 20 }}>
          {/* <Div style={{ wordWrap: "break-word", marginBottom: 5 }}>
            {senderName}
          </Div> */}
          <Div style={{ color: "green" }}>+ {amount} руб</Div>
        </Div>
      }
      asideContent={
        <Div
          style={{
            marginTop: "-28px",
            display: "flex",
            justifyContent: "end",
            flexDirection: "row",
            alignItems: "end",
            fontSize: "10px",
          }}
        >
          {formatDate(createdAt)}
        </Div>
      }
    >
      <Div
        style={{
          marginBottom: "5px",
          paddingLeft: "15px",
          display: "flex",
          justifyContent: "start",
          flexDirection: "row",
          alignItems: "center",
          fontSize: "16px",
        }}
      >
        <Div>
          <Icon16Up fill="green" />
        </Div>
        <Div style={{ marginTop: 4, marginLeft: 4 }}>Покупка</Div>
      </Div>
    </Cell>
  );
};

export { OperationsHistoryItem };
