import React, { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Link,
  InfoRow,
  Input,
  FormLayoutGroup,
  FormLayout,
  Button,
} from "@vkontakte/vkui";
import Icon28WriteOutline from "@vkontakte/icons/dist/28/write_outline";

import {
  OperationsHistoryItem,
} from "../items";
import {
  dataHistory,
} from "../helpers";



const Statistics = ({


}) => {

  const [dataOperationsHistory, setDataOperationsHistory] = useState([]);

  // useEffect(() => {
  //   getOperationsHistory();
  // }, []);

  // function getOperationsHistory() {
  //   callMethod('getTransfersHistory')
  //     .then(({ status, message }) => {
  //       setDataOperationsHistory(message);
  //       console.log("getOperationsHistory", dataOperationsHistory);
  //     });
  // }

  function formatDate(date) {
    date = new Date(date * 1000);

    let dd = date.getDate();
    if (dd < 10) dd = "0" + dd;
    let mm = date.getMonth() + 1;
    if (mm < 10) mm = "0" + mm;
    let yy = date.getFullYear() % 100;
    if (yy < 10) yy = "0" + yy;
    let hh = date.getHours();
    if (hh < 10) hh = "0" + hh;
    let minutes = date.getMinutes();
    if (minutes < 10) minutes = "0" + minutes;

    return dd + "." + mm + "." + yy + " " + hh + ":" + minutes;
  }



  const renderHistory = dataHistory.map((item, index) => {
    const senderUrl = "https://vk.com/id" + item.senderId;

    return (
      <OperationsHistoryItem
        key={index}
        id={index + 1}
        senderUrl={senderUrl}
        amount={item.amount}
        senderName={item.senderName}
        senderPhoto={item.senderPhoto}
        createdAt={item.createdAt}
        formatDate={formatDate}
      />
    );
  });

  return (
    <Div
      style={{
        margin: 10,
      }}
    >
        <Cell >
          <Div
          >
            <Group separator="show">
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              marginLeft: 15
              }}
            >
              Статистика по группе:
            </Div>
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              marginLeft: 15
              }}
            >
              Название группы
            </Div>
            </Group>
            <Group separator="show">
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
                marginLeft: 15
              }}
            >
              За сегодня: 299 руб
            </Div>
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
                marginLeft: 15
              }}
            >
              За все время: 1599 руб
            </Div>
            </Group>
           
           {renderHistory}

          </Div>

          
        </Cell>
  
    </Div>
  );
};

export { Statistics };
